.card-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: black;
  font-weight: bold;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

.custom-card:hover {
  cursor: pointer;
}

.custom-card:hover .card-text {
  opacity: 1;
}

.image-container {
  overflow: hidden; /* Cache le contenu qui dépasse */
  transition: filter 0.3s, transform 0.3s; /* Ajoutez une transition fluide */
}

.image-container:hover {
  filter: blur(5px) grayscale(25%); /* Effets de flou et de teinte grisâtre */
  transform: scale(1.1); /* Effet de zoom */
}

.card-img {
  transition: transform 0.3s; /* Ajoutez une transition au zoom */
}

.card-img:hover {
  transform: scale(1.1); /* Effet de zoom au survol de l'image */
}
